export class Test {
  viterbi;
  constructor(viterbi) {
    this.viterbi = viterbi;
  }
  _randint(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
  }
  runTests(count) {
    let tests = [];
    for (let i = 0; i < this.viterbi.constraint; i++) {
      tests[i] = [];
      let arr = [];
      for (let j = 0; j < count; j++) {
        let seed = this._randint(2 ** (this.viterbi.constraint + 2), 2 ** (this.viterbi.constraint + 16)).toString(2);
        arr.push(seed);
      }
      arr.map((seed) => this.viterbi.encode(seed).then(result => {
        let len = result.length;
        let error = [...Array(i)].fill(1).join("").padEnd(this._randint(0, len - i + 1), "0").substr(0, len).padStart(len, "0");
        let u = (BigInt("0b" + result) ^ BigInt("0b" + error)).toString(2);
        this.viterbi.decode(u).then((decoded) => {
          tests[i].push(decoded == seed, decoded, seed);
        })
      }))
    }
    console.log(tests);
  }
}

export default Test